export default{
	data(){
		return{
			levls:''
		}
	},
	methods:{
		jump(url){
			uni.switchTab({
				url:url
			})
		}
	},
	onShow() {
		this.$service.auth(this , this.$service.api.service.team_level_list , 'POST' , {
			page:1,
			limit:15
		},function(self , res){
			console.log(res);
			self.levls = res.data
		});
		
	}
}
